<template>
  <div class="com-popup">
    <p class="com-title">{{ popupName }}</p>
    <div class="com-content">
      <p class="item-title">{{ itemName }}</p>
      <div class="right-info com_content">
        <p v-for="(i, index) in hisCom">
          <input type="checkbox" v-model="name" @click="chooseName(i)" :value="i">
          {{ i }}
        </p>
      </div>
    </div>
    <div class="com-foot">
      <template v-for="button in buttons">
        <button-style :buttonName="button" @clickFun="comClick(button)"></button-style>
      </template>
    </div>
  </div>
</template>

<script>
  import ButtonStyle from '@/components/buttonStyle'

  export default{
    name: 'com-popup',
    components: {
      ButtonStyle
    },
    props: {
      popupName: '',    // 弹窗名称
      buttons: '',   // 按钮
      itemName: '',   // 左边标题
      item: ''    // 审批单基本信息
    },
    mounted: function () {
      $.scrollFix('com_content')
    },
    methods: {
      comClick: function (button) {
        let pop = new Array()
        if (this.name.length > 0) {
          pop = this.name[0].split('(')
          this.$emit('comClick', button, pop[0])
        } else {
          this.$emit('comClick', button, '')
        }
      },
      // 设置单选
      chooseName: function (id) {
        if (this.name.indexOf(id) !== -1) {
          this.name = []
          this.name.push(id)
        } else {
          this.name = []
        }
      }
    },
    data: function () {
      return {
        hisCom: ['钱二(总行_金融市场部)', '孙三(总行_金融市场部)', '小明(总行_计财部)'],    // 历史代办人
        name: []    // 选中代办人
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .com-popup{
    position fixed
    width 13.8rem
    height 11.5rem
    background-color #ffffff
    z-index 10
    border-radius 0.5rem
    left: 50%;
    top: 45%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    .com-title{
      position absolute
      top 0
      left 0
      width 100%
      height 1.8rem
      line-height 1.8rem
      border-radius 0.5rem 0.5rem 0 0
      background-color #4FA4D9
      color #ffffff
      text-align center
      font-size 0.75rem
    }
    .com-content{
      position absolute
      width 100%
      height 100%
      padding 2rem 0.8rem 3rem 0.8rem
      font-size 0.65rem
      display flex
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .item-title{
        display flex
        width 1rem
        margin-left 0.2rem
        height 100%
        color grey
        font-size 0.65rem
        justify-content center
        align-items center
      }
      .right-info{
        flex 1
        overflow-y auto
        height 100%
        padding 0.5rem
        -webkit-box-sizing: border-box
        -moz-box-sizing: border-box
        box-sizing: border-box
        p{
          width 100%
          height 1.2rem
          display flex
          align-items center
        }
        input {
          width 0.7rem
          height 0.7rem
          margin-right 0.3rem
        }
      }
    }
    .com-foot{
      position absolute
      bottom 0
      left 0
      width 100%
      height 3rem
      border-radius 0 0 0.5rem 0.5rem
      display flex
      justify-content space-between
      align-items center
      padding 0 2.5rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .button_button{
        height 1.4rem
      }
    }
  }
</style>



