<template>
  <section class="check-item" @click="itemClick">
    <div class="left-info">
      <p>{{ item.itemName }}</p>
      <p>审批发起人： <a :href="'tel:' + item.tel" @click.stop="">{{ item.initiator }}({{ item.initiateBank }})</a></p>
      <p>审批发起时间： {{ item.time }}</p>
      <p v-if="type === 'approved'">审批结果： {{item.result}}</p>
    </div>
    <i class="fa fa-chevron-right"></i>
  </section>
</template>

<script>
  export default{
    name: 'check-item',
    props: {
      item: '',   // 显示字段
      type: ''    // 类型
    },
    methods: {
      itemClick: function () {
        this.$emit('itemClick', this.item.id, this.type)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .check-item{
    width 100%
    height auto
    background #ffffff
    overflow hidden
    border-top 1px solid #dddddd
    border-bottom 1px solid #cccccc
    display flex
    flex-direction row
    align-items center
    margin-bottom 0.3rem
    &:active{
      background darken(#ffffff, 3%)
    }
    .left-info{
      width 14rem
      padding 0.3rem 0.5rem
      display inline-block
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      p{
        height 1rem
        line-height 1rem
        font-size 0.65rem
        &:first-child{
          height 1.2rem
          line-height 1.2rem
        }
        a{
          color #4FA4D9
        }
      }
    }
    .fa-chevron-right{
      width 2rem
      height inherit
      display inline-block
      text-align center
      font-size 0.7rem
    }
  }
</style>



