// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import VueFlatpickr from 'vue-flatpickr'
// import 'vue-flatpickr/theme/material_green.css'
import './assets/js/fastclick.min'
import './assets/js/scrollfix'
import './assets/js/optimise_tool'

Vue.config.productionTip = false
Vue.use(VueAwesomeSwiper)
Vue.use(VueFlatpickr)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  data: {
    eventHub: new Vue(),
    checkWait: [    // 待审批
      {id: '00011', itemName: '金融机构理财产品', initiator: '李健', initiateBank: '浦东分行', time: '2017-05-15', result: '', tel: '13800001111'},
      {id: '00012', itemName: '信用保证保险下金融机构理财产品', initiator: '李健', initiateBank: '浦东分行', time: '2017-05-15', result: '', tel: '13800001111'}
    ],
    approved: [   // 已审批
      {id: '10013', itemName: '信用保证保险下金融机构理财产品', initiator: '李健', initiateBank: '浦东分行', time: '2017-05-15', result: '已竞签(竞签人: 王五)', tel: '13800001111'}
    ],
    organization: [   // 机构树
      {
        id: '0',
        name: '总行',
        options: [
          {
            id: '00',
            name: '金融市场部',
            options: [
              {id: '0000', name: '赵一'},
              {id: '0001', name: '钱二'},
              {id: '0002', name: '孙三'},
              {id: '0003', name: '李四'}
            ]
          },
          {
            id: '01',
            name: '计财部',
            options: [
              {id: '0100', name: '小明'},
              {id: '0101', name: '小白'},
              {id: '0102', name: '小红'}
            ]
          }
        ]
      }
    ],
    effectivity: [    // 有效授权
      {id: '001', name: '钱二(总行_金融市场部)', type: 1, times: '2017-5-19 9:00 - 2017-5-25 17:00'},
      {id: '002', name: '孙三(总行_金融市场部)', type: 0, times: '单次授权(金融机构理财产品)'}
    ],
    loseEfficacy: [   // 失效授权
      {id: '001', name: '孙三(总行_金融市场部)', type: 0, times: '2017-5-11 9:00 - 2017-5-18 17:00'}
    ]
  },
  methods: {
    // 流程处理
    flowFun: function (type, id, name) {
      for (let i = 0; i < this.checkWait.length; i++) {
        if (this.checkWait[i].id === id) {
          let item = this.checkWait[i]
          this.checkWait.splice(i, 1)
          switch (type) {
            case '提交':
              item.result = '已通过'
              this.approved.unshift(item)
              break
            case '退回':
              item.result = '已退回'
              this.approved.unshift(item)
              break
            case '代办':
              item.result = '已代办(代办人:' + name + ')'
              this.approved.unshift(item)
              break
            default:
              break
          }
        }
      }
    },
    // 取消授权
    cancelAcc: function (id) {
      for (let i=0; i < this.effectivity.length; i++) {
        if (this.effectivity[i].id === id) {
          let item = this.effectivity[i]
          this.effectivity.splice(i, 1)
          item.type = 0
          this.loseEfficacy.unshift(item)
        }
      }
    }
  }
})



