<script>
  export default{
    name: 'search',
    props: {
      pla: {   // 提示语
        type: '',
        default: '搜索'
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .search-nav{
    position relative
    width 100%
    height 2rem
    background-color #CCCCCC
    z-index 4
    input{
      width 14.6rem
      height 1.4rem
      margin 0.3rem 0.7rem
      border none
      border-radius 0.4rem
      outline none
      font-size 0.6rem
      padding 0 0.5rem 0 1.4rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
    }
    i{
      position absolute
      left 0.7rem
      top 0.3rem
      width 1.4rem
      height 1.4rem
      color grey
      text-align center
      line-height 1.4rem
      font-size 0.6rem
    }
  }
</style>



