<template>
  <div class="hint-popup">
    <p class="hint-title">{{ hintName }}</p>
    <div class="hint-content">
      <p class="item-title">{{ itemName }}</p>
      <div class="right-info">
        <p>{{ item.itemName }}</p>
        <p>审批发起人： <a :href="'tel:' + item.tel" @click.stop="">{{ item.initiator }}({{ item.initiateBank }})</a></p>
        <p>审批发起时间： {{ item.time }}</p>
      </div>
    </div>
    <div class="hint-foot">
      <template v-for="button in buttons">
        <button-style :buttonName="button" @clickFun="hintClick(button)"></button-style>
      </template>
    </div>
  </div>
</template>

<script>
  import ButtonStyle from '@/components/buttonStyle'

  export default{
    name: 'hint-popup',
    components: {
      ButtonStyle
    },
    props: {
      hintName: '',    // 弹窗名称
      buttons: '',   // 按钮
      itemName: '',   // 左边标题
      item: ''    // 审批单基本信息
    },
    methods: {
      hintClick: function (button) {
        this.$emit('hintClick', button)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .hint-popup{
    position fixed
    width 13.8rem
    height 10.5rem
    background-color #ffffff
    z-index 10
    border-radius 0.5rem
    left: 50%;
    top: 45%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    .hint-title{
      position absolute
      top 0
      left 0
      width 100%
      height 1.8rem
      line-height 1.8rem
      border-radius 0.5rem 0.5rem 0 0
      background-color #4FA4D9
      color #ffffff
      text-align center
      font-size 0.75rem
    }
    .hint-content{
      position absolute
      width 100%
      height 100%
      padding 2rem 0.8rem 2.5rem 0.8rem
      font-size 0.65rem
      display flex
      flex-direction column
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .item-title{
        width 100%
        color grey
        font-size 0.65rem
      }
      .right-info{
        flex 1
        display flex
        flex-direction column
        padding-left 1rem
        -webkit-box-sizing: border-box
        -moz-box-sizing: border-box
        box-sizing: border-box
        p{
          font-size 0.7rem
          height 1.2rem
          &:first-child{
            margin-top 1rem
          }
          a{
            color #4FA4D9
          }
        }
      }
    }
    .hint-foot{
      position absolute
      bottom 0
      left 0
      width 100%
      height 2.5rem
      border-radius 0 0 0.5rem 0.5rem
      display flex
      justify-content space-between
      padding 0 2.5rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .button_button{
        height 1.4rem
      }
    }
  }
</style>



