<template>
  <div class="choose-popup">
    <p class="popup-title">{{ popupName }}</p>
    <div class="organization">
      <Organization v-if="type === '代办'" :items="items" @changeValue="changeVal"></Organization>
      <Organizations v-if="type === '加签'" :items="items" @changeValue="changeVal"></Organizations>
    </div>
    <div class="popup-foot">
      <button-style buttonName="取消" @clickFun="popupClick('取消')"></button-style>
      <button-style buttonName="确定" @clickFun="popupClick('确定')" :disabled="isDisabled"></button-style>
    </div>
  </div>
</template>

<script>
  import ButtonStyle from '@/components/buttonStyle'
  import Organization from '@/components/organization'
  import Organizations from '@/components/organizations'

  export default{
    name: 'choose-popup',
    components: {
      ButtonStyle,
      Organization,
      Organizations
    },
    props: {
      popupName: '',    // 弹窗名称
      type: ''   // 选择类型
    },
    data: function () {
      return {
        items: '',    // 机构树
        isDisabled: true,    // 确定按钮
        name: ''    // 机构选中的人
      }
    },
    created: function () {
      this.items = this.$root.organization
    },
    mounted: function () {
      $.scrollFix('organization')
    },
    methods: {
      // 弹窗按钮功能
      popupClick: function (button) {
        this.$emit('popupClick', button, this.name)
      },
      // 机构选择
      changeVal: function (name) {
        this.name = name
        if (name) {
          this.isDisabled = false
        } else {
          this.isDisabled = true
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .choose-popup{
    position fixed
    width 12rem
    height 16.8rem
    background-color #ffffff
    z-index 10
    border-radius 0.5rem
    left: 50%;
    top: 45%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    .popup-title{
      position absolute
      top 0
      left 0
      width 100%
      height 1.8rem
      line-height 1.8rem
      border-radius 0.5rem 0.5rem 0 0
      background-color #4FA4D9
      z-index 2
      color #ffffff
      text-align center
      font-size 0.75rem
    }
    .organization{
      position absolute
      top 0
      left 0
      padding 2rem 0.8rem 3rem 0.8rem
      width 100%
      height 100%
      overflow-x hidden
      overflow-y auto
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
    }
    .popup-foot{
      position absolute
      bottom 0
      left 0
      width 100%
      height 3rem
      background-color #ffffff
      z-index: 2
      border-radius 0 0 0.5rem 0.5rem
      display flex
      justify-content space-between
      align-items center
      padding 0 2.5rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .button_button{
        height 1.4rem
      }
    }
  }
</style>



