import Vue from 'vue'
import Router from 'vue-router'
import Check from '@/components/check/check'
import myCheck from '@/components/check/my-check'
import checkMain from '@/components/check/check-main'
import checkFlow from '@/components/check/check-flow'
import Set from '@/components/set/set'
import SetIndex from '@/components/set/set-index'
import newCom from '@/components/set/new-commission'
import com from '@/components/set/commission'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Check',
      component: Check,
      children: [
        {
          path: '/',
          component: checkMain
        }
      ]
    },
    {
      path: '/check',
      name: 'Check',
      component: Check,
      children: [
        {
          path: '/',
          component: checkMain
        },
        {
          path: 'check-main',
          component: checkMain
        },
        {
          path: 'my-check',
          component: myCheck
        },
        {
          path: 'check-flow',
          component: checkFlow
        }
      ]
    },
    {
      path: '/set',
      name: 'set',
      component: Set,
      children: [
        {
          path: '/',
          component: SetIndex
        },
        {
          path: 'set-index',
          component: SetIndex
        },
        {
          path: 'new-com',
          component: newCom
        },
        {
          path: 'com',
          component: com
        }
      ]
    }
  ]
})



