<template>
  <div class="set-index">
    <Head-info :name="headInfo.name" :isBack="headInfo.isBack"></Head-info>
    <div class="main">
      <div class="head-div">
        <div class="img-div">
          <img src="../../assets/img/head_img.png" alt="头像">
        </div>
        <p>王行长</p>
        <p>上海银行副行长</p>
      </div>
      <div class="set-content">
        <set-items v-for="item in items" :itemName="item.name"
                   :items="item.options" @setItem="goSet"></set-items>
      </div>
    </div>
  </div>
</template>

<script>
  import HeadInfo from '@/components/head'
  import setItems from '@/components/set/set-items'

  export default{
    name: 'set-index',
    components: {
      HeadInfo,
      setItems
    },
    data: function () {
      return {
        headInfo: '',    // 头部信息
        items: ''   // 设置模块
      }
    },
    created: function () {
      this.headInfo = {name: '个人设置', isBack: true}
      this.items = [
        {
          id: '0',
          name: '安全设置',
          options: [
            {id: '00', name: '设置图形解锁'},
            {id: '01', name: '设置指纹解锁'}
          ]
        },
        {
          id: '1',
          name: '代办设置',
          options: [
            {id: '10', name: '新建代办'},
            {id: '11', name: '查看已设置代办'}
          ]
        }
      ]
    },
    mounted: function () {
      $.scrollFix('main')
    },
    methods: {
      // 跳转设置页面
      goSet: function (id) {
        if (id === '10') {
          this.$router.push('/set/new-com')
        } else if (id === '11') {
          this.$router.push('/set/com')
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .set-index{
    width inherit
    height 100%
    position relative
    overflow-x hidden
    overflow-y hidden
    background-color #F7F7F7
    .head-div{
      width 100%
      height 6.7rem
      display block
      overflow hidden
      .img-div{
        width 3.6rem
        height 3.6rem
        border-radius 50%
        display: block;
        overflow: hidden;
        margin 0.5rem auto 0.2rem auto
        border 1px solid #dddddd
        img{
          width 3.6rem
          height 3.6rem
        }
      }
      p{
        display table
        height 1rem
        line-height 1rem
        margin 0 auto
        font-size 0.7rem
      }
    }
  }
</style>



