<template>
  <header class="head">
    <a v-show="isBack" href="javascript: void (0)" @click="goBack"><s class="go-back"></s>返回</a>
    {{ name }}
    <i :class="iClass" @click="iconFun"></i>
  </header>
</template>

<script>
  export default {
    name: 'head',
    props: {
      name: '',    // 头部名称
      isBack: Boolean,   // 返回按钮
      iClass: ''    // 右侧图标
    },
    data: function () {
      return {
        isClose: false   // 关闭按钮
      }
    },
    methods: {
      goBack: function () {
        this.$router.go(-1)
      },
      // 图标按钮功能
      iconFun: function () {
        this.$emit('iconFun')
      }
    }
  }
</script>

<style lang="stylus">
  .head{
    width 100%
    height 2.4rem
    position relative
    text-align center
    line-height 2.4rem
    font-size 0.85rem
    border-bottom 1px solid #B2B2B2
    background-color #F7F7F7
    z-index 4
    a{
      position absolute
      top 0
      left 0
      width 2.8rem
      height 2.4rem
      line-height 2.4rem
      display block
      font-size 0.75rem
      padding-right 0.4rem
      color #4FA4D9
      &:active{
        background-color darken(#f7f7f7, 5%)
      }
      .go-back{
        width 1rem
        height inherit
        vertical-align: top;
        display: inline-block;
        margin-left  0.1rem
        margin-right -0.2rem
        background url("../assets/img/go-back.png")no-repeat center
        background-size contain
      }
    }
    p{
      position absolute
      top 0
      left 3.4rem
      font-size 0.75rem
      color #4FA4D9
      &:active{
        background-color darken(#f7f7f7, 5%)
      }
    }
    i{
      position absolute
      right 0
      top 0
      width 2.4rem
      height 2.4rem
      line-height 2.4rem
      font-size 1rem
      color #4FA4D9
      &:active{
        background-color darken(#f7f7f7, 5%)
      }
    }
  }
</style>



