<template>
  <button @click="clickFun" class="button_button" :class="[buttonStyle, {'disabled': disabled}]" :disabled="disabled">
    <a href="javascript:void(0)">{{ buttonName }}</a>
  </button>
</template>

<script>
  export default {
    name: 'buttonStyle',
    props: {
      buttonStyle: '',      // 按钮样式
      buttonName: '',        // 按钮名称
      disabled: false   // 是否可以点击
    },
    methods: {
      clickFun: function () {
        this.$emit('clickFun')
      }
    }
  }
</script>

<style lang="stylus">
  .button_button{
    height 1.8rem
    background-color: #44b549;
    border: none;
    color: #fff;
    font-size: 0.7rem;
    border-radius: 0.3rem;
    &:active{
      background darken(#44b549, 10%)
    }
    a{
      width 100%
      padding 0 0.8rem
      display block
      color: #fff;
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
    }
  }
  .disabled{
    background-color grey
    &:active{
      background darken(grey, 3%)
    }
  }
</style>



