<template>
  <div class="hint-error-popup">
    <p class="com-title">{{ popupName }}</p>
    <div class="com-content">
      <p class="right-info">{{ text }}</p>
    </div>
    <div class="com-foot">
      <button-style buttonName="确定" @clickFun="errClick()"></button-style>
    </div>
  </div>
</template>

<script>
  import ButtonStyle from '@/components/buttonStyle'

  export default{
    name: 'hint-error-popup',
    components: {
      ButtonStyle
    },
    props: {
      popupName: '',    // 弹窗名称
      text: ''   // 左边标题
    },
    methods: {
      errClick: function () {
        this.$emit('errClick')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .hint-error-popup{
    position fixed
    width 13.8rem
    height 8.5rem
    background-color #ffffff
    z-index 10
    border-radius 0.5rem
    left: 50%;
    top: 45%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    .com-title{
      position absolute
      top 0
      left 0
      width 100%
      height 1.8rem
      line-height 1.8rem
      border-radius 0.5rem 0.5rem 0 0
      background-color #4FA4D9
      color #ffffff
      text-align center
      font-size 0.75rem
    }
    .com-content{
      position absolute
      width 100%
      height 100%
      padding 2rem 0.8rem 3rem 0.8rem
      font-size 0.65rem
      display flex
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .item-title{
        display flex
        width 1rem
        margin-left 0.2rem
        height 100%
        color grey
        font-size 0.65rem
        justify-content center
        align-items center
      }
      .right-info{
        flex 1
        overflow-y auto
        height 100%
        font-size 0.75rem
        text-align center
        padding 0.5rem
        -webkit-box-sizing: border-box
        -moz-box-sizing: border-box
        box-sizing: border-box
      }
    }
    .com-foot{
      position absolute
      bottom 0
      left 0
      width 100%
      height 3rem
      border-radius 0 0 0.5rem 0.5rem
      display flex
      justify-content center
      align-items center
      padding 0 2.5rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .button_button{
        height 1.4rem
      }
    }
  }
</style>



