<template>
  <div class="set-item">
    <p class="title">{{ itemName }}</p>
    <p v-for="item in items" @click="setItem(item.id)">{{ item.name }}</p>
  </div>
</template>

<script>
  export default{
    name: 'set-items',
    props: {
      itemName: '',    // 大类
      items: ''   // 小类
    },
    methods: {
      // 设置项目
      setItem: function (id) {
        this.$emit('setItem', id)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .set-item{
    width 100%
    height auto
    display block
    padding 0.5rem 0
    border-top 1px solid #cccccc
    p{
      font-size 0.7rem
      width 100%
      height 1.2rem
      line-height 1.2rem
      display block
      padding-left 2.5rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      &:active:not(.title){
        background darken(#f7f7f7, 3%)
      }
    }
    .title{
      padding-left 1rem
    }
  }
</style>



