<template>
  <nav class="nav-tab" :class="navStyle">
    <div class="nav-div">
      <template v-for="(item, index) in items">
        <a href="javascript: void (0)" :class="{'is-active': item.isActive}"
           @click="clickNav(index)">{{ item.name }}({{ item.num }})</a>
      </template>
    </div>
  </nav>
</template>

<script>
  export default{
    name: 'nav-tab',
    props: {
      items: '',   // tab内容
      navStyle: ''    // nav样式
    },
    methods: {
      clickNav: function (index) {
        this.$emit('clickNav', index)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .nav-tab{
    position relative
    width 16rem
    height 2rem
    overflow hidden
    background-color #CCCCCC
    z-index 4
    .nav-div{
      width auto
      height 1.4rem
      display table
      margin 0.3rem auto
      border 1px solid #dddddd
      border-radius 1rem
      overflow hidden
      background-color #ffffff
      a{
        min-width 4rem
        font-size 0.7rem
        padding 0 0.3rem
        text-align center
        display inline-block
        height inherit
        line-height 1.4rem
        border-right 1px solid #cccccc
        color grey
        &:last-child{
          border-right none
        }
      }
      .is-active{
        background-color #4FA4D9
        color #ffffff
      }
    }
  }
</style>



