<template>
  <div class="check-flow">
    <Head-info :name="headInfo.name" :isBack="headInfo.isBack" :iClass="headInfo.iClass"></Head-info>
    <ul class="main">
      <li class="header">
        <p>审批角色</p>
        <p>审批人</p>
        <p>审批意见</p>
      </li>
      <li v-for="flow in flows">
        <p>{{ flow.role }}</p>
        <p>{{ flow.name }}</p>
        <p>{{ flow.opinion }}</p>
      </li>
    </ul>
  </div>
</template>

<script>
  import HeadInfo from '@/components/head'

  export default{
    name: 'check-flow',
    components: {
      HeadInfo
    },
    data:function () {
      return {
        id: '',    // 审批单id
        checkName: '',   // 审批单名称
        headInfo: '',   // 头部信息
        flows: []    // 流程
      }
    },
    created: function () {
      this.id = this.$route.query.id
      this.checkName = this.$route.query.checkName
      this.headInfo = {name: this.checkName, isBack: true}
      this.flows = [
        {role: '浦东分行-营销管理岗', name: '李健', opinion: ''},
        {role: '浦东分行-营销部门负责人岗', name: '吴小龙', opinion: '同意'}
      ]
    },
    mounted: function () {
      $.scrollFix('main')
    },
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .check-flow {
    width inherit
    height 100%
    position relative
    overflow-x hidden
    overflow-y hidden
    background-color #F7F7F7
    ul{
      li{
        display flex
        flex-wrap nowrap
        justify-content space-between
        align-items center
        border-bottom 1px solid #cccccc
        p{
          width 4rem
          padding 0.5rem
          text-align center
          font-size 0.65rem
          -webkit-box-sizing: border-box
          -moz-box-sizing: border-box
          box-sizing: border-box
          &:nth-child(1){
            width 5.5rem
          }
          &:nth-child(3){
            width 6rem
          }
        }
      }
      .header{
        background-color #4FA4D9
        p{
          color #ffffff
          font-size 0.75rem
        }
      }
    }
  }
</style>



