<template>
  <div class="new-com">
    <Head-info :name="headInfo.name" :isBack="headInfo.isBack"></Head-info>
    <div class="choose-time">
      <label>代办开始时间:</label>
      <Flatpickr placeholder="请选择开始时间" :options="startOptions" v-model="startTime"></Flatpickr>
    </div>
    <div class="choose-time">
      <label>代办结束时间:</label>
      <Flatpickr class="endTime" placeholder="请选择结束时间" :options="endOptions" v-model="endTime"></Flatpickr>
    </div>
    <div class="main organization">
        <p class="com-name"><span>选择代办人</span></p>
        <Organization :items="items" @changeValue="changeVal"></Organization>
    </div>
    <div class="new-com-foot">
      <button-style buttonName="确定" class="foot-button" @clickFun="newComClick()" :disabled="isDisabled"></button-style>
    </div>
    <hint-error v-show="isError" popupName="错误提示" :text="errorText" @errClick="closePopup"></hint-error>
    <!--蒙层-->
    <div v-show="shadowShow" class="shadow" @click="closePopup"></div>
  </div>
</template>

<script>
  import HeadInfo from '@/components/head'
  import Organization from '@/components/organization'
  import ButtonStyle from '@/components/buttonStyle'
  import HintError from '@/components/hint-error'
  import '../../assets/js/format'

  export default{
    name: 'new-com',
    components: {
      HeadInfo,
      Organization,
      ButtonStyle,
      HintError
    },
    data: function () {
      return {
        items: '',    // 机构树
        name: '',   // 选中机构名
        headInfo: '',    // 头部信息
        isDisabled: true,    // 确定按钮
        startTime: '',    // 开始时间
        endTime: '',    // 结束时间
        startOptions: '',   // 开始时间选择设置
        endOptions: '',    // 结束时间选择设置
        errorText: '',   // 错误信息
        shadowShow: false,    // 蒙层
        isError: false    // 错误提示
      }
    },
    created: function () {
      let t = new Date().getTime()
      t += 900000
      let startDate = new Date(t).Format("yyyy-MM-dd hh:mm")
      let endDate = new Date().Format("yyyy-MM-dd") + ' 17:00'
      if (endDate < startDate) {
        endDate = new Date(t + 3600000).Format("yyyy-MM-dd hh:mm")
      }
      this.startOptions = {
        disableMobile: false,
        dateFormat: 'Y-m-d H:i',
        enableTime: true,
        enableMinute: false,
        time_24hr: true,
        defaultDate: startDate,
        minDate: startDate
      }
      this.endOptions = {
        disableMobile: false,
        dateFormat: 'Y-m-d H:i',
        enableTime: true,
        time_24hr: true,
        defaultDate: endDate,
        minDate: startDate
      }
      this.headInfo = {name: '新建代办', isBack: true}
      this.items = this.$root.organization
    },
    mounted: function () {
      $.scrollFix('organization')
    },
    methods: {
      // 机构选择
      changeVal: function (name) {
        this.name = name
        if (name) {
          this.isDisabled = false
        } else {
          this.isDisabled = true
        }
      },
      // 确定
      newComClick: function () {
        let date = new Date().Format("yyyy-MM-dd hh:mm")
        if (this.startTime < date) {
          this.errorText = '开始时间小于当前时间'
          this.isError = true
          this.shadowShow = true
        } else if (this.endTime < this.startTime) {
          this.errorText = '结束时间小于开始时间'
          this.isError = true
          this.shadowShow = true
        } else {
//          let item = {
//            id: '00' + this.$root.effectivity.length
//          }
          this.$router.push('/set')
        }
      },
      // 关闭弹窗
      closePopup: function () {
        this.isError = false
        this.shadowShow = false
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .new-com{
    width inherit
    height 100%
    position relative
    overflow-x hidden
    overflow-y hidden
    background-color #F7F7F7
    .choose-time{
      position relative
      width 100%
      height 1.8rem
      display flex
      align-items center
      padding 0 0.8rem
      font-size 0.7rem
      background-color #F7F7F7
      z-index 4
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      &:first-child{
        margin-top 0.5rem
      }
      label{
        width 5rem
      }
      input{
        background-color #F7F7F7
        border none
        border-bottom 1px solid #cccccc
        border-radius 0
        display block
        width 7.5rem
        height 1rem
        outline none
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        padding 0 0.3rem
      }
    }
    .organization{
      padding-top 7rem
      .com-name{
        position relative
        font-size 0.7rem
        height 1.2rem
        line-height 1.2rem
        &:before{
          position absolute
          top 0.6rem
          left 0
          width 2.8rem
          height 2px
          content: ''
          display: block
          background: #cccccc
        }
        &:after{
          position absolute
          top 0.6rem
          right 0
          width 9.1rem
          height 2px
          content: ''
          display: block
          background: #cccccc
        }
        span{
          margin-left 3rem
          font-size 0.75rem
        }
      }
      .org{
        padding 0.5rem 1.2rem
      }
    }
    .new-com-foot{
      position fixed
      bottom 0
      left 0
      width 100%
      height 2.4rem
      background-color #F7F7F7
      z-index 4
      border-top 1px solid #cccccc
      display flex
      justify-content center
      align-items center
      padding 0 0.8rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      .foot-button{
        padding 0 0.8rem
      }
    }
  }
</style>



