<template>
  <section class="com-item">
    <div class="left-info">
      <p>{{ item.name }}</p>
      <p>{{ item.times }}</p>
      <button-style v-if="item.type" buttonName="取消授权" @clickFun="cancelAcc"></button-style>
    </div>
  </section>
</template>

<script>
  import ButtonStyle from '@/components/buttonStyle'

  export default{
    name: 'com-item',
    components: {
      ButtonStyle
    },
    props: {
      item: ''   // 显示字段
    },
    methods: {
      // 取消授权
      cancelAcc: function () {
        this.$root.cancelAcc(this.item.id)
        this.$emit('cancelAcc')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .com-item{
    width 100%
    height auto
    background #ffffff
    overflow hidden
    border-top 1px solid #dddddd
    border-bottom 1px solid #cccccc
    display flex
    flex-direction row
    align-items center
    margin-bottom 0.3rem
    .left-info{
      width 15.5rem
      padding 0.3rem 0.5rem
      display inline-block
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
      p{
        line-height 1.2rem
        font-size 0.65rem
      }
      .button_button{
        height 1.2rem
        float right
        background-color #4FA4D9
        &:active{
          background darken(#4FA4D9, 10%)
        }
      }
    }
  }
</style>



