<template>
  <div class="my-check">
    <Head-info :name="headInfo.name" :isBack="headInfo.isBack"
               :iClass="headInfo.iClass" @iconFun="checkFlow"></Head-info>
    <article class="main" :class="{'my-check-main': isBottom}">
      <swiper :options="swiperOption">
        <!-- 要渲染的那些内容 -->
        <swiper-slide v-for="item in items">
          <p class="title">{{ item.name }}</p>
          <div class="content">
            <template v-if="item.type">
              <template v-for="second in item.item">
                <p class="second-title">{{ second.name }}</p>
                <template v-for="(cont, index) in second.item">
                  <p class="field second-field">
                    <span class="key">{{ index }}:</span>
                    <span class="value">{{ cont }}</span>
                  </p>
                </template>
              </template>
            </template>
            <template v-else="">
              <template v-for="(cont, index) in item.item">
                <p class="field">
                  <span class="key">{{ index }}:</span>
                  <span class="value">{{ cont }}</span>
                </p>
              </template>
            </template>
          </div>
        </swiper-slide>
        <!-- 轮播的小圆点 -->
        <div class="swiper-pagination" :class="{'pag-bottom': isBottom}" slot="pagination"></div>
      </swiper>
    </article>
    <footer v-if="isBottom" class="footer">
      <template v-for="item in buttonItem">
        <button-style :buttonName="item" @clickFun="buttonClick(item)"></button-style>
      </template>
    </footer>
    <!--蒙层-->
    <!--<div v-show="shadowShow" class="shadow"></div>-->
    <div v-show="shadowShow" class="shadow" @click="closePopup"></div>
    <hint-popup v-show="isHintPopup" :hintName="popupName" :buttons="popupButtons"
                :itemName="itemName" :item="baseItem" @hintClick="hintFun"
                :type="popupType"></hint-popup>
    <choose-popup v-show="isChoosePopup" :popupName="popupName" :type="popupType" :buttons="popupButtons"
                  @popupClick="comFun"></choose-popup>
    <com-popup v-show="isComPopup" popupName="请选择对象" itemName="历史代办人" type="代办" :buttons="popupButtons"
               @comClick="comFun"></com-popup>
  </div>
</template>

<script>
  import HeadInfo from '@/components/head'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import ButtonStyle from '@/components/buttonStyle'
  import HintPopup from '@/components/hint-popup'
  import ChoosePopup from '@/components/choose-popup'
  import ComPopup from '@/components/commission-popup'

  export default{
    name: 'my-check',
    components: {
      HeadInfo,
      swiper,
      swiperSlide,
      ButtonStyle,
      HintPopup,
      ChoosePopup,
      ComPopup
    },
    data: function () {
      return {
        id: '',   // 审批单id
        headInfo: '',   // 头部信息
        items: '',   // 审批详情
        baseItem: '',   // 审批单基本信息
        isBottom: '',    // 审批类型
        swiperOption: {     // 轮播设置
          notNextTick: true,
          pagination: '.swiper-pagination',
          slidesPerView: 'auto',
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 30
        },
        buttonItem: ['代办', '加签', '退回', '提交'],
        shadowShow: false,    // 蒙层
        isHintPopup: false,   // 提示窗是否显示
        isChoosePopup: false, // 选择机构
        isComPopup: false, // 代办
        popupButtons: '',    // 弹窗的按钮
        popupName: '',   // 弹窗名称
        itemName: '',   // 弹窗左边名称
        popupType: ''    // 弹窗类型
      }
    },
    created: function () {
      this.id = this.$route.query.id
      if (this.$route.query.type === 'checkWait') {
        this.isBottom = true
      }
      const data = {
        checkName: '金融机构理财产品',
        checkInfo: [
          {
            name: '审批单基础信息',
            type: 0,
            item: {
              '业务名称': '金融机构理财产品',
              '审批发起人': '李健',
              '审批发起机构': '浦东分行',
              '发起人固话': '0571-1234567',
              '发起人手机': '13800001111',
              '审批开始日期': '2017-05-15'
            }
          },
          {
            name: '参与方',
            type: 1,
            item: [
              {
                name: '一、发行人',
                type: 0,
                item: {
                  '客户号': '3222125936',
                  '客户名称': '晋商银行',
                  '联系人': '王三',
                  '联系电话': '18066668888'
                }
              }
            ]
          },
          {
            name: '业务信息',
            type: 0,
            item: {
              '币种': 'CNY',
              '本金(元)': '1,000,000.00',
              '利率类型': '固息',
              '基准利率代码': 'FIXED',
              '利率点差(BP)': '0',
              '预期收益率': '3%',
              '综合收益率': '3%',
              '逾期罚息率': '4%',
              '指导价': '1%',
              'FTP': '',
              '起息日期': '2017-05-20',
              '划款日期': '2017-05-20',
              '到期日期': '2018-01-31',
              '到账日期': '2018-01-31',
              '计息天数': '256',
              '资金占用天数': '256',
              '计息基础': '365',
              '还本频率': '到期一次性还本',
              '收息频率': '到期一次性收',
              '到期利息(元)': '28,054.79',
              '到期金额(元)': '1,028,054.79',
              '补充说明': ''
            }
          },
          {
            name: '业务结构',
            type: 0,
            item: {
              '产品名称': '金融机构理财产品',
              '产品类型': '保本保收益',
              '是否审批开户': '是',
              '受益权优先级标志': '优先',
              '平台投向': '其他',
              '最终投向': '权益类',
              '投资范围': '',
              '资产会计四分类': '贷款及应收款',
              '备注': ''
            }
          },
          {
            name: '风险资产及缓释',
            type: 0,
            item: {
              '基础资产项下风险资产类型': '我国商业银行(25%)',
              '风险资产金额': '1,000,000.00',
              '风险缓释工具': '现金类资产缓释金额',
              '风险缓释金额': '1,000,000.00',
              '风险资产总额(元)': '1,000,000.00',
              '风险缓释总额(元)': '1,000,000.00',
              '缓释后风险资产总额(元)': '0',
              '缓释后风险资产占比(%)': '0'
            }
          },
          {
            name: '额度管理',
            type: 0,
            item: {
              '是否占用同业额度': '',
              '授信品种': '同业投资',
              '同业授信可用金额': '10,000,000,000',
              '占用企业额度': '1,000,000.00',
              '企业授信可用金额': '1,000,000.00',
              '占用金额': '1,000,000.00'
            }
          }
        ]
      }
      this.headInfo = {name: data.checkName, isBack: true, iClass: 'fa fa-codepen'}
      this.items = data.checkInfo
      this.baseItem = {id: '00011', itemName: '金融机构理财产品', initiator: '李健', initiateBank: '浦东分行', time: '2017-05-15', tel: '13800001111'}
    },
    mounted: function () {
      $.scrollFix('main')
    },
    methods: {
      // 流程按钮
      buttonClick: function (name) {
        this.popupType = name
        if (name === '提交') {
          this.popupName = '您确认要提交本次审批吗？'
          this.itemName = ''
          this.popupButtons = ['取消', '确定']
          this.isHintPopup = true
        } else if (name === '退回') {
          this.popupName = '您确认要退回本次审批吗？'
          this.itemName = ''
          this.popupButtons = ['取消', '确定']
          this.isHintPopup = true
        } else if (name === '加签') {
          this.popupName = '请选择加签对象？'
          this.popupButtons = ['取消', '确定']
          this.isChoosePopup = true
        } else if (name === '代办') {
          this.popupName = '请选择代办对象？'
          this.popupButtons = ['取消', '新增／确定']
          this.isComPopup = true
        }
        this.shadowShow = true
      },
      // 弹窗关闭
      closePopup: function () {
        this.shadowShow = false
        this.isHintPopup = false
        this.isChoosePopup = false
        this.isComPopup = false
      },
      // 退回、提交弹窗功能
      hintFun: function (name) {
        if (name === '取消') {
          this.closePopup()
        } else {
          this.$root.flowFun(this.popupType, this.id, '')
          this.closePopup()
          this.$router.push({ path: '/Check/check-main', query: { isCheckWait: '1'}})
        }
      },
      // 代办、加签弹窗功能
      comFun: function (name, pop) {
        if (name === '取消') {
          this.closePopup()
        } else {
          if (pop) {
            this.$root.flowFun(this.popupType, this.id, pop)
            this.closePopup()
            this.$router.push({ path: '/Check/check-main', query: { isCheckWait: '1'}})
          } else {
            this.popupButtons = ['取消', '确定']
            this.isComPopup = false
            this.isChoosePopup = true
          }
        }
      },
      // 跳转流程图
      checkFlow: function () {
        this.$router.push({ path: '/Check/check-flow', query: { id: this.id, checkName: this.headInfo.name}})
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .my-check{
    width inherit
    height 100%
    position relative
    overflow-x hidden
    overflow-y hidden
    background-color #F7F7F7
    .head{
      font-size 0.75rem
    }
    .main {
      background-color #ffffff
      .swiper-container{
        width 100%
        min-height 100%
        overflow-x hidden
        overflow-y hidden
        .title{
          text-align center
          font-size 0.8rem
          height 2rem
          line-height 2rem
          -webkit-box-sizing: border-box
          -moz-box-sizing: border-box
          box-sizing: border-box
        }
        .swiper-slide{
          height 10px
          .content{
            padding  0 0.8rem
            -webkit-box-sizing: border-box
            -moz-box-sizing: border-box
            box-sizing: border-box
            .field{
              font-size 0.7rem
              line-height 1.2rem
              display flex
              &:last-child{
                margin-bottom 1rem
              }
              .key{
                width 5rem
              }
              .value{
                flex 1
                padding-left 0.3rem
                display flex
                align-items center
              }
            }
            .second-title{
              font-size 0.75rem
            }
            .second-field{
              padding-left 0.5rem
              -webkit-box-sizing: border-box
              -moz-box-sizing: border-box
              box-sizing: border-box
            }
          }
        }
        .swiper-slide-active{
          height auto
          overflow-y auto
        }
        .swiper-pagination{
          position fixed
          bottom 0.5rem
          .swiper-pagination-bullet{
            width 16px
            height 16px
          }
        }
        .pag-bottom{
          bottom 2.6rem
        }
      }
    }
    .my-check-main{
      padding-bottom 2.4rem
    }
    .footer{
      position fixed
      bottom 0
      left 0
      width 100%
      height 2.4rem
      background-color #F7F7F7
      z-index 4
      border-top 1px solid #cccccc
      display flex
      justify-content space-between
      align-items center
      padding 0 0.8rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
    }
  }
</style>



