<template>
  <div class="orgs">
    <ul class="first" v-for="(first, index) in items">
      <li>
        <p class="title" @click="firstChangeState(index)">
          <i class="fa fa-minus-square-o" ref="firstIcon"></i>
          {{ first.name }}
        </p>
        <ul class="second" ref="second">
          <li v-for="(second, index) in first.options">
            <p class="title" @click="changeState(index)">
              <i class="fa fa-minus-square-o" ref="secondIcon"></i>
              {{ second.name }}
            </p>
            <ul class="third" ref="third">
              <p class="name" v-for="third in second.options" >
                <input type="checkbox" v-model="isCheckbox" :value="third.name">
                {{ third.name }}
              </p>
            </ul>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
  export default{
    name: 'organization',
    props: {
      items: ''   // 机构数据
    },
    data: function () {
      return {
        isCheckbox: []    // 选中的值
      }
    },
    methods: {
      // 设置单选
//      chooseName: function (id) {
//        if (this.isCheckbox.indexOf(id) !== -1) {
//          this.isCheckbox = []
//          this.isCheckbox.push(id)
//        } else {
//          this.isCheckbox = []
//        }
//      },
      // 一级目录折叠展开
      firstChangeState: function (index) {
        const $this = this.$refs
        if ($this.firstIcon[index].className === 'fa fa-minus-square-o') {
          $this.second[index].style.display = 'none'
          $this.firstIcon[index].className = 'fa fa-plus-square-o'
        } else {
          $this.second[index].style.display = ''
          $this.firstIcon[index].className = 'fa fa-minus-square-o'
        }
      },
      // 二级目录折叠展开
      changeState: function (index) {
        const $this = this.$refs
        if ($this.secondIcon[index].className === 'fa fa-minus-square-o') {
          $this.third[index].style.display = 'none'
          $this.secondIcon[index].className = 'fa fa-plus-square-o'
        } else {
          $this.third[index].style.display = ''
          $this.secondIcon[index].className = 'fa fa-minus-square-o'
        }
      }
    },
    watch: {
      isCheckbox (val) {
        if (val.length > 0) {
          this.$emit('changeValue', val[0])
        } else {
          this.$emit('changeValue', '')
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .orgs{
    ul.first{
      font-size 0.7rem
      i{
        font-size 0.8rem
        margin-right 0.3rem
      }
      p{
        display flex
        align-items center
      }
      .second{
        padding-left 0.9rem
      }
      .third{
        padding-left 0.9rem
        input {
          width 0.7rem
          height 0.7rem
          margin-right 0.3rem
        }
      }
    }
  }
</style>



