<template>
  <div class="check-index" ref="checkIndex">
    <Head-info :name="headInfo.name" :isBack="headInfo.isBack"
               :iClass="headInfo.iClass" @iconFun="goSet"></Head-info>
    <Search pla="搜索"></Search>
    <Nav-tab :items="navItems" navStyle="check-nav" @clickNav="changNav"></Nav-tab>
    <article class="main check-main">
      <template v-if="isCheckWait">
        <template v-for="item in checkWait">
          <Check-item :item="item" type="checkWait" @itemClick="goDetails"></Check-item>
        </template>
      </template>
      <template v-else="">
        <template v-for="item in approved">
          <Check-item :item="item" type="approved" @itemClick="goDetails"></Check-item>
        </template>
      </template>
    </article>
  </div>
</template>

<script>
  import HeadInfo from '@/components/head'
  import Search from '@/components/search'
  import NavTab from '@/components/nav-tab'
  import CheckItem from '@/components/check/check-item'

  export default{
    name: 'check-main',
    components: {
      HeadInfo,
      Search,
      NavTab,
      CheckItem
    },
    data: function () {
      return {
        headInfo: {name: '移动端审批', isBack: false, iClass: 'fa fa-user'},   // 头部信息
        navItems: [   // tab页
          {name: '待审批', num: this.$root.checkWait.length, isActive: true},
          {name: '已审批', num: this.$root.approved.length, isActive: false}
        ],
        checkWait: '',    // 待审批内容
        approved: '',   // 已审批内容
        isCheckWait: true   // 是否为待审批，false为已审批
      }
    },
    created: function () {
      this.checkWait = this.$root.checkWait
      this.approved = this.$root.approved
      if (this.$route.query.isCheckWait === '1') {
        this.navItems[0].isActive = false
        this.navItems[1].isActive = true
        this.isCheckWait = false
      }
    },
    mounted: function () {
      $.scrollFix('check-main')
    },
    methods: {
      // 待审批/已审批切换
      changNav: function (index) {
        // 切换
        for (let nav of this.navItems) {
          nav.isActive = false
        }
        this.navItems[index].isActive = true
        // 搜索对应内容
        if (this.navItems[index].name === '待审批' ) {
          this.isCheckWait = true
        } else {
          this.isCheckWait = false
        }
      },
      // 跳转审批详情
      goDetails: function (id, type) {
        this.$router.push({ path: '/Check/my-check', query: { id: id, type: type}})
      },
      // 跳转个人设置
      goSet: function () {
        this.$router.push('/set')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .check-index{
    width inherit
    height 100%
    position relative
    overflow-x hidden
    overflow-y hidden
    background-color #F7F7F7
    .check-nav{
      margin-top -0.3rem
    }
    .check-main{
      padding-top 6.1rem
    }
  }
</style>



