<template>
  <div class="commission">
    <Head-info :name="headInfo.name" :isBack="headInfo.isBack"></Head-info>
    <Search pla="搜索"></Search>
    <Nav-tab :items="navItems" navStyle="com-nav" @clickNav="changNav"></Nav-tab>
    <article class="main commission-main">
      <template v-if="isEffectivity">
        <template v-for="item in effectivity">
          <Com-item :item="item" @cancelAcc="update"></Com-item>
        </template>
      </template>
      <template v-else="">
        <template v-for="item in loseEfficacy">
          <Com-item :item="item" @cancelAcc="update"></Com-item>
        </template>
      </template>
      <button-style buttonName="增加" buttonStyle="add-com" @clickFun="addCom"></button-style>
    </article>
  </div>
</template>

<script>
  import HeadInfo from '@/components/head'
  import Search from '@/components/search'
  import NavTab from '@/components/nav-tab'
  import ComItem from '@/components/set/com-items'
  import ButtonStyle from '@/components/buttonStyle'

  export default{
    name: 'commission',
    components: {
      HeadInfo,
      Search,
      NavTab,
      ComItem,
      ButtonStyle
    },
    data: function () {
      return {
        headInfo: '',    // 头部信息
        navItems: '',    // tab页
        effectivity: '',   // 有效代办授权内容
        loseEfficacy: '',    // 失效
        isEffectivity: true   // 有效显示
      }
    },
    created: function () {
      this.headInfo = {name: '查看代办规则', isBack: true}
      this.effectivity = this.$root.effectivity
      this.loseEfficacy = this.$root.loseEfficacy
      this.navItems = [
        {name: '有效代办授权', num: this.$root.effectivity.length, isActive: true},
        {name: '失效代办授权', num: this.$root.loseEfficacy.length, isActive: false}
      ]
    },
    methods: {
      changNav: function (index) {
        // 切换
        for (let nav of this.navItems) {
          nav.isActive = false
        }
        this.navItems[index].isActive = true
        // 搜索对应内容
        if (this.navItems[index].name === '有效代办授权' ) {
          this.isEffectivity = true
        } else {
          this.isEffectivity = false
        }
      },
      // 更新数据
      update: function () {
        this.navItems[0].isActive = false
        this.navItems[1].isActive = true
        this.navItems[0].num = this.$root.effectivity.length
        this.navItems[1].num = this.$root.loseEfficacy.length
        this.isEffectivity = false
      },
      // 新增代办
      addCom: function () {
        this.$router.push('/set/new-com')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .commission{
    width inherit
    height 100%
    position relative
    overflow-x hidden
    overflow-y hidden
    background-color #F7F7F7
    .com-nav{
      margin-top -0.3rem
    }
    .commission-main{
      padding-top 6.1rem
    }
    .add-com{
      display block
      margin 0.5rem auto
      padding 0 0.6rem
      background-color #4FA4D9
      &:active{
        background darken(#4FA4D9, 10%)
      }
    }
  }
</style>



